@import url(https://fonts.googleapis.com/css2?family=Play&display=swap);
body{margin:0;padding:0;color:black;font-size:14px;font-family:'Play', sans-serif}body div{margin:0;padding:0;box-sizing:border-box}body p{margin:0;padding:0}#root{width:100%;height:100vh;display:flex;justify-content:center;align-items:center;flex-direction:column}#root h1{color:gray;font-size:50px}#root .box{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none}#root .container{margin:20px;border:1px solid;display:grid;min-height:400px;font-size:25px;grid-template-rows:1fr 1fr 1fr 1fr 1fr;grid-template-columns:repeat(4, minmax(70px, 180px));background-color:#000;grid-gap:1px;font-weight:bold;box-shadow:0 0 10px 3px gray;outline-style:none}@media screen and (max-width: 400px){#root .container .result{font-size:25px !important}}#root .container .result{display:flex;align-items:center;font-size:35px;grid-column:1/5;border:1px solid #f2274f;justify-content:flex-end;cursor:pointer;font-size:35px;padding:10px;color:#fff;background-color:#433437;cursor:pointer}#root .container .digits{grid-column:1/4;grid-row:2/6;color:#433437;background-color:#000}#root .container .digits .d-grid{display:grid;width:100%;height:100%;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(4, 1fr);grid-gap:1px}@media screen and (max-width: 400px){#root .container .digits .d-grid .clear{font-size:20px}}#root .container .digits .d-grid .clear{color:#7a0000}#root .container .digits .box{display:flex;justify-content:center;align-items:center;background-color:#fff;font-size:35px;cursor:pointer;transition:0.3s}#root .container .digits .box:hover{opacity:0.9}#root .container .digits .box:active{transform:scale(0.95);background-color:#006080;color:#fff;opacity:1}#root .container .actions{display:grid;height:100%;grid-template-columns:1fr;grid-template-rows:repeat(4, 1fr);grid-column:4/5;grid-row:2/6;grid-gap:1px}#root .container .actions .action{background-color:#f2274f;color:#fff;font-size:50px;transition:0.3s}#root .container .actions .box{display:flex;justify-content:center;align-items:center;font-size:45px;cursor:pointer}#root .container .actions .box:hover{opacity:0.6}#root .container .actions .box:active{transform:scale(0.95);background-color:maroon;color:#fff;opacity:1}

